.Branch{
    position: relative;
    display: flex;
    width: calc(100%/4);
    margin: 0 10px;
    img{
        width: 100%;
        height: 100%;
        display: flex;
    }
    .Overlay{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,0.5);
        vertical-align: middle;
        display: flex;
        justify-content: center;
        align-items: center;
        h4{
            color: white;
        }
    }
}